.block-contact {
  width: 100%;
  padding: 50px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(var(--secondary-color), 1);
}
@media (min-width: 1024px) {
  .block-contact {
    padding: 120px 0;
  }
}
.block-contact .block__inner {
  max-width: 1800px;
  width: 95%;
  margin: 0 auto;
  display: flex;
}
.block-contact .cols {
  width: 100%;
  height: 100%;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  column-gap: 14rem;
  row-gap: 3rem;
}
.block-contact .cols .col {
  min-width: 280px;
  display: flex;
  align-self: flex-start;
  justify-content: center;
  flex: 1 0 calc(50% - 7rem);
}
.block-contact .cols .col:first-child {
  flex-direction: column;
  gap: 30px;
}
.block-contact .cols .col .block-title {
  font-family: var(--adore);
  font-size: 6.8rem;
  font-weight: 700;
  line-height: 1.2;
  color: rgba(var(--light-grey), 1);
}
@media (max-width: 1023px) {
  .block-contact .cols .col .block-title {
    font-size: 36px;
  }
}
.block-contact .cols .col .contact {
  margin-left: 40px;
  font-family: var(--montserrat);
  font-size: 2.5rem;
  line-height: 1.2;
  color: rgb(var(--light-grey));
  text-decoration: underline;
}
.block-contact .cols .col .contact:hover {
  color: rgb(var(--primary-color));
}
.block-contact .cols .col .heading,
.block-contact .cols .col .heading a {
  font-family: var(--montserrat);
  font-size: 2.8rem;
  font-weight: 600;
  line-height: 3rem;
  color: rgba(var(--text-color), 1);
}
.block-contact .cols .col .form {
  width: 100%;
  display: flex;
}
.block-contact .cols .col .wpforms-container {
  width: 100%;
}
.block-contact .cols .col .wpforms-container .wpforms-form {
  width: 100%;
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 25px;
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container select,
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container option,
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container input,
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container textarea {
  max-width: 100%;
  padding: 18px 0;
  font-family: var(--montserrat);
  font-size: 1.6rem;
  line-height: 3rem;
  color: rgb(var(--light-grey));
  border-bottom: 1px solid rgb(var(--light-grey));
  border-radius: 0;
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container select::placeholder,
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container option::placeholder,
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container input::placeholder,
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container textarea::placeholder {
  color: rgb(var(--primary-color));
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container select:focus, .block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container select:active,
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container option:focus,
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container option:active,
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container input:focus,
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container input:active,
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container textarea:focus,
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container textarea:active {
  outline: 1px solid rgb(var(--light-grey));
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container option {
  background-color: rgb(var(--secondary-color));
  border: none;
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container option:focus, .block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container option:hover {
  background-color: rgb(var(--tertiary-color));
  outline: none;
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field-label {
  margin-bottom: 10px;
  color: rgb(var(--light-grey));
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field {
  max-width: 100%;
  width: 100%;
  padding: 0 0;
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-name .wpforms-field-row,
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-name fieldset {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-name .wpforms-field-row .wpforms-field-row-block,
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-name fieldset .wpforms-field-row-block {
  display: flex;
  flex: 1 0 calc(50% - 10px);
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-name .wpforms-field-row .wpforms-field-row-block input,
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-name fieldset .wpforms-field-row-block input {
  width: 100%;
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-checkbox ul {
  margin: 10px 0;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-checkbox ul li {
  width: fit-content;
  height: fit-content;
  padding: 5px 10px;
  position: relative;
  border: 1.5px solid rgb(var(--light-grey));
  border-radius: 25px;
  cursor: pointer;
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-checkbox ul li:hover, .block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-checkbox ul li.wpforms-selected, .block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-checkbox ul li:has(input:checked) {
  background-color: rgb(var(--primary-color));
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-checkbox ul li input {
  width: 100%;
  height: 100%;
  margin: 0;
  top: 0;
  left: 0;
  position: absolute;
  z-index: 10;
  opacity: 0;
  cursor: pointer;
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field.wpforms-field-checkbox ul li label {
  position: relative;
  z-index: 0;
  font-size: 1.6rem;
  color: rgb(var(--light-grey));
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field fieldset {
  flex: 0 0 100%;
  display: flex;
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field fieldset .wpforms-field-row {
  max-width: 100%;
  flex: 0 0 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field fieldset .wpforms-field-row::after, .block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field fieldset .wpforms-field-row::before {
  content: none;
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-field-container .wpforms-field fieldset .wpforms-field-row .wpforms-field-row-block {
  width: 100%;
  margin-bottom: 0;
  padding: 0;
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-submit-container {
  margin-top: 20px;
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-submit-container [type=submit] {
  appearance: none;
  padding: 16px 28px;
  background-color: rgb(var(--secondary-color));
  border: none;
  text-transform: capitalize;
  font-weight: normal;
  font-size: 2rem;
  color: #fff;
  cursor: pointer;
}
.block-contact .cols .col .wpforms-container .wpforms-form .wpforms-submit-container [type=submit]:hover {
  background-color: rgb(var(--primary-color));
}